.container {
  max-width: 375px;

  margin: 0 auto;
  padding: 0 20px;

  @media screen and (min-width: 768px) {
    max-width: none;
    width: 768px;
    padding: 0px 32px;
  }

  @media screen and (min-width: 1440px) {
    width: 1440px;
    padding: 0px 24px;
  }
}

.page-wrapper {
  @media screen and (min-width: 1440px) {
    display: flex;
    gap: 40px;
  }
}